import { Component, OnInit } from '@angular/core';
import { Coupon } from 'src/app/models/coupon';
import { LoginService } from 'src/app/service/LoginService';
import { CustomerServiceService } from 'src/app/service/customer-service.service';

@Component({
  selector: 'app-purchase-coupon',
  templateUrl: './purchase-coupon.component.html',
  styleUrls: ['./purchase-coupon.component.css']
})
export class PurchaseCouponComponent implements OnInit {

  constructor(private myService:CustomerServiceService) { }
  coupons:Coupon[]
  coupon:Coupon
  id:number
  ngOnInit() {
    this.myService.getAllCouponsInTotal().subscribe(
      coupons=>{
        this.coupons = coupons
      },err=>{
        alert("something went wrong...")
      }
    )
  }
   
  public purchaseCoupon(){
 this.myService.purchaseCoupon(this.coupon).subscribe(
   coup=>{

     this.coupon=coup
   },err=>{
     alert("failed to purchase coupon")
   }
 )
  }
}
