import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Coupon } from '../models/coupon';
import { category } from '../models/category';
import { customer } from '../models/customer';

@Injectable({
  providedIn: 'root'
})
export class CustomerServiceService {

  constructor(private httpClient: HttpClient) { }

  public getAllCoupons(): Observable<Coupon[]>{
    return this.httpClient.get<Coupon[]>("/Customer/getAllCoupons/"+localStorage.myToken)
  }
  public purchaseCoupon(coupon:Coupon){
    return this.httpClient.post<Coupon>("/Customer/purchaseCoupon/" + localStorage.myToken,coupon);
  }
  public getAllCouponsInTotal(): Observable<Coupon[]>{
    return this.httpClient.get<Coupon[]>("/Customer/getAllCouponsInTotal/" + localStorage.myToken);
  }
  public getCouponsByMaxPrice(maxPrice:number){
    return this.httpClient.get<Coupon[]>("/Customer/getCouponsByMaxPrice/" + localStorage.myToken+"/"+maxPrice);
  }
  public getCouponsByCategory(category:category){
    return this.httpClient.get<Coupon[]>("/Customer/getCouponsByCategory/" + localStorage.myToken+"/"+category);
 }

 public getCustomerDetails(): Observable<customer>{
  return this.httpClient.get<customer>("/Customer/getCustomerDetails/" + localStorage.myToken);
}
}
