import { Component, OnInit } from '@angular/core';
import { CustomerServiceService } from 'src/app/service/customer-service.service';
import { category } from 'src/app/models/category';
import { Coupon } from 'src/app/models/coupon';

@Component({
  selector: 'app-get-all-coupons-by-category',
  templateUrl: './get-all-coupons-by-category.component.html',
  styleUrls: ['./get-all-coupons-by-category.component.css']
})
export class GetAllCouponsByCategoryComponent implements OnInit {

  constructor(private myService:CustomerServiceService) { }
  category:category
  coupons:Coupon[]
  options: string[] = Object.keys(category)
    ngOnInit() {
      this.options = this.options.slice(this.options.length/2)
    }
    public getCouponsByCategory(){
      this.myService.getCouponsByCategory(this.category).subscribe(coups=>{
        this.coupons = coups;
      }, err=>{
        alert("oops! "+ err.error)
      })
    }
  }
