import { Component, OnInit } from '@angular/core';
import { CustomerServiceService } from 'src/app/service/customer-service.service';
import { Coupon } from 'src/app/models/coupon';

@Component({
  selector: 'app-get-all-coupons-in-total',
  templateUrl: './get-all-coupons-in-total.component.html',
  styleUrls: ['./get-all-coupons-in-total.component.css']
})
export class GetAllCouponsInTotalComponent implements OnInit {

  constructor(private myService:CustomerServiceService) { }
   coupons:Coupon[];
   coupon:Coupon
  ngOnInit() {
    this.myService.getAllCouponsInTotal().subscribe(
      coupons=>{
        this.coupons = coupons
      },err=>{
        alert("something went wrong...")
      }
    )
  }
  public purchaseCoupon(c:Coupon){
    this.myService.purchaseCoupon(c).subscribe(
      coup=>{
   
        c=coup
        alert("coupon successfully purchased !")
      },err=>{
        alert("oops! "+ err.error)
      }
    )
     }
}
