import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './components/home/home.component';
import { PurchaseCouponComponent } from './components/purchase-coupon/purchase-coupon.component';
import { GetAllCouponsComponent } from './components/get-all-coupons/get-all-coupons.component';
import { GetAllCouponsInTotalComponent } from './components/get-all-coupons-in-total/get-all-coupons-in-total.component';
import { GetAllCouponsByCategoryComponent } from './components/get-all-coupons-by-category/get-all-coupons-by-category.component';
import { GetCouponsByMaxPriceComponent } from './components/get-coupons-by-max-price/get-coupons-by-max-price.component';
import { GetCustomerDetailsComponent } from './components/get-customer-details/get-customer-details.component';


const routes: Routes = [
{path: "home", component: HomeComponent},
{path: "purchase-coupon", component: PurchaseCouponComponent},
{path: "get-all-coupons", component: GetAllCouponsComponent},
{path: "get-all-coupons-in-total", component: GetAllCouponsInTotalComponent},
{path: "get-all-coupons-by-category", component: GetAllCouponsByCategoryComponent},
{path: "get-coupons-by-max-price", component: GetCouponsByMaxPriceComponent},
{path: "get-customer-details", component: GetCustomerDetailsComponent},
{path: "", redirectTo:"home", pathMatch: "full"}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
